.code h1 {
    margin-top: 33vh;
    font-family: Aldrich;
    font-size: 8vw;
    text-align: center;
    transition:cubic-bezier(0.075, 0.82, 0.165, 1) 1000ms;
  }
  
  .code--clicked h1{
    margin-top: 8rem;
    font-size: 5vw;
  }
  
  .code {
    width: 50vw;
    height: 83vh;
    position: absolute;
    right: 0;
    background-image: url('C:\Users\rkbk9\Documents\WebDev\firstreactapp\codeBG.png');
    background-size: cover;
    text-align: center;
    font-size: 4vw;
    color: #d7c3fc;
    margin-left: 0;
    transition: all 1000ms;
    cursor: url('C:\Users\rkbk9\Documents\WebDev\firstreactapp\src\LogoCursorL.png'), auto;
  }
  .code:hover {
    color:red;
  }
  .code--clicked {
    width: 100vw;
    height:fit-content;
    text-align: justify;
    overflow: hidden;
  }
  .code--clicked:hover {
    color: #d7c3fc;
  }
  
  .codePg{
    margin: 0 auto;
    margin-top: 0px;
    font-size: 1rem;
  }

  .codePg p{
    line-height: 1.5;
    max-width: 70%;
    margin: 0 auto;
    margin-bottom: 2rem;
  }

  .codeProjects {
    margin-top: 10rem;
    display: block;
    text-align: center;
  }

  iframe{
    margin: 0 auto;
    margin-top: 3rem;
    display: block;
    width: 30rem;
    height: 20rem;
    border-style: solid;
    border-width: 5pt;
    border-color: #83738a;
    border-radius: 2pt;
  }
  canvas {
    border: 1px solid black;
    background-color: aliceblue;
  }

  .my-code-project{
    padding-bottom: 10rem;   
    border-top-style: groove;
    border-width: 1.5pt;
    border-color: #1d0f3a65;
    background-color: rgba(54, 14, 233, 0.068);
  }
  .my-code-project:nth-child(even){
    background-color: rgba(219, 0, 47, 0.068);
  }
  .mobile-message {
    visibility: collapse;
  }

  .footer {
    align-content: center;
    text-align: center;
    padding: 3px;
    background-color: rgba(0, 0, 0, 0.486);
    color: white;
    display: block;
    width: 100vw;
    font-size: 1.2rem;

  }

  .footer a{
    text-decoration: none;
    color: white;
  }


  .nav-list{
    display: flex;
    margin: 0;
    padding: 0;
    padding-top: 3rem;
    list-style: none;
    gap: 1.2rem;
    flex-wrap: wrap;
    justify-content: center;
    height: 5rem;


  }
  .footer-icon{
    font-size: 1.5rem;
    width: 1.5rem;
  }
  .codeMobile {
    visibility: hidden;
    height: 0;
  }

  .codeDesktop {
    visibility: visible;
    height:fit-content;
    width: 70%;
    margin: 0 auto;
  }

  @media screen and (max-width: 500px){
    .expandParagraphsC {
      visibility: visible;
      width: 80vw;
      height: auto;
      border-style: solid;
      border-color: #eadfff;
      border-radius: 3px;
      margin-top: 3rem;
      margin: 0 auto;
      margin-bottom: 3rem;
    }

    .expandParagraphsC p {
      font-size: 12px;
    }


    .expandParagraphsC h2 {
      font-size: 95%;
      line-height: 3;
      text-align: center;
    }


    .codeDesktop {
      visibility: hidden;
      height: 0;
    }

    .codeMobile {
      visibility: visible;
      height:fit-content;
    }


    .code--clicked h1{
      margin-top: 2rem;
      font-size: 5rem;
    }
    .codeProjects {
      visibility: hidden;
      height: 0;
    }

    .mobile-message{
      visibility: visible;
      max-width: 70%;
      margin: 0 auto;  
      margin-top: -8rem;
      margin-bottom: 3rem;
    }

    .codePg{
      font-size: .8rem;
    }




  }